<template>
	<div id="mealRedpacket-index">
		<public-header title="套餐红包购买"></public-header>
		<div class="content">
			<div class="meal-container">
				<div class="top">
					<div class="name">选择套餐红包</div>
					<div class="tip">
						提示信息
						<span>{{redpack.placeholder}}</span>
					</div>
				</div> 
				<div class="meal-item" v-for="(item,index) in redpack.redpackets">
					<div class="item-container" @click='selectRedpacket(index,item.price,item.old_price)' :class="{active: index == select}">
						<div class="left">{{item.name}}</div>
						<div class="right">
							<span class="old-price">¥<span>{{item.old_price}}</span></span>
							<span class="price">¥<span>{{item.price}}</span></span>
						</div>
					</div>
				</div>
			</div>
			<div class="redpacket-wrap" v-for="(packets,index) in redpack.redpackets" v-if=" index == select">
				<div class="redpacket-wrap-title ">{{packets.name}}红包套餐内容</div>
				<div class="redpacket-item" v-for="(redpacket,index) in packets.data">
					<div class="redpacket-item-body">
						<div class="item-body-price">
							￥
							<span class="body-price-number"><strong>{{redpacket.discount}}</strong></span>
						</div>
						<div class="item-body-name">
							<div class="name-general">{{redpacket.name}}</div>
							<div class="name-reduction">满{{redpacket.condition}}元可用</div>
						</div>
					</div>
				</div>
			</div>
			<router-link :to="util.getUrl({path: '/pages/mealRedpacket/order'})" class="buy-record van-hairline--top van-hairline--bottom">
				购买记录
				<van-icon name='right'></van-icon>
			</router-link>
			<div class="meal-explain">
				套餐规则
				<van-icon name='question'></van-icon>
			</div>
			<div class="submit-container">
				<div class="final-price">
					<div class="text">总价</div>
					<div class="price">￥<span>{{price}}</span></div>
					<div class="old-price">￥<span>{{old_price}}</span></div>
				</div>
				<div class="submit-button">立即购买</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader
		},
  		data() {
  			return {
  				redpack: {},
  				select: 0,
  				price: 0,
  				old_price: 0
  			}
  		},
  		methods: {
			onLoad() {
				this.util.request({
					url: 'superRedpacket/meal'
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					this.redpack = result.message.data;
				})
			},
			selectRedpacket(index,price,old_price) {
				this.select = index;
				this.price = price;
				this.old_price = old_price;
			}
		},
  		mounted() {
  			this.onLoad();
  		}
	}
</script>

<style>
	#mealRedpacket-index .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 50px;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
        overflow-y: scroll;
	}
	#mealRedpacket-index .content::-webkit-scrollbar {
        display: none;
    }
    #mealRedpacket-index .van-nav-bar{
	    background-color: #ff2d4b!important;
	    color: #fff!important;
    }
    #mealRedpacket-index .van-hairline--bottom .van-icon-left{
    	color: #fff!important;
    }
    #mealRedpacket-index .content .meal-container{
		width: 100%;
		background-color: #fff;
		box-sizing: border-box;
		padding: 20px 15px;
    }
    #mealRedpacket-index .content .meal-container .top{
    	margin-bottom: 10px;
    }
    #mealRedpacket-index .content .meal-container .top .name{
		font-size: 16px;
		color: #3F3F3F;
		height: 26px;
		line-height: 26px;
    }
    #mealRedpacket-index .content .meal-container .top .tip{
		font-size: 12px;
		color: #7B7B79;
		max-width: 100%;
		line-height: 18px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
    }
    #mealRedpacket-index .content .meal-container .top .tip span{
    	color: #FF5D43;
    }
    #mealRedpacket-index .content .meal-container .meal-item{
		width: 100%;
		margin-bottom: 10px;
    }
    #mealRedpacket-index .content .meal-container .meal-item:last-child{
	    margin-bottom: 0;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container.active{
	    background-color: #FFFDEF;
	    border: .1rem solid #E9D8A8;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container{
	    border: 2px solid #ddd;
	    padding: 15px;
	    min-height: 25px;
	    display: flex;
	    flex-flow: row nowrap;
	    align-items: center;
	    justify-content: space-between;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container .left{
		width: 70%;
		overflow: hidden;
		color: #333;
		font-size: 14px;
		font-weight: bold;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container .right{
		width: 30%;
		text-align: right;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container .right .old-price{
		color: #B4B3B0;
		font-size: 12px;
		text-decoration: line-through;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container .right .price{
		color: #FF5338;
		font-size: 14px;
		font-weight: 600;
    }
    #mealRedpacket-index .content .meal-container .meal-item .item-container .right .price span{
    	font-weight: 600;
    }
    #mealRedpacket-index .content .redpacket-wrap{
		height: auto;
		background: #fff;
		padding: 20px 4px;
		overflow: hidden;
		padding-top: 0;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-wrap-title{
		font-size: 16px;
		color: #3f3f3f;
		padding: 0 11px;
		margin-bottom: 10px;
		line-height: 24px;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item{
		width: 46%;
		background: #fff7e7;
		float: left;
		border-radius: 3px;
		margin-bottom: 10px;
		margin-left: 10px;
		overflow: hidden;
		padding: 10px;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body{
	    overflow: hidden;
	    display: flex;
	    flex-flow: row nowrap;
	    align-items: center;
	    justify-content: space-between;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-price{
		width: 48%;
		color: #fb5227;
		text-align: left;
		white-space: nowrap;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-price .body-price-number{
    	font-size: 38px;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-price .body-price-number strong{
    	font-weight: 700;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-name{
		width: 50%;
		text-align: left;
		height: 52px;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-name .name-general{
		height: 26px;
		font-size: 16px;
		line-height: 26px;
		color: #000;
		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
    }
    #mealRedpacket-index .content .redpacket-wrap .redpacket-item .redpacket-item-body .item-body-name .name-reduction{
		display: inline-block;
		height: 26px;
		line-height: 26px;
		font-size: 14px;
		color: #bf9a42;
		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
    }
    #mealRedpacket-index .content .meal-explain{
		display: block;
		width: 100%;
		font-size: 12px;
		color: #717171;
		text-align: center;
		margin-top: 20px;
		overflow: hidden;
		margin-bottom: 20px;
    }
    #mealRedpacket-index .content .meal-explain .van-icon{
		display: inline-block;
		border: 0.1px solid #717171;
		width: 12px;
		height: 12px;
		border-radius: 50%;
		text-align: center;
		line-height: 12px;
		margin-left: 3px;
    }
    #mealRedpacket-index .content .submit-container{
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 50px;
		box-sizing: border-box;
		padding-left: 15px;
		line-height: 50px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		background: #3A3635;
		color: #fff;
    }
    #mealRedpacket-index .content .submit-container .final-price{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
    }
    #mealRedpacket-index .content .submit-container .final-price .text{
    	font-size: 16px;
    }
    #mealRedpacket-index .content .submit-container .final-price .price{
		font-size: 16px;
		color: #FF5338;
		margin: 0 5px;
    }
    #mealRedpacket-index .content .submit-container .final-price .price span{
    	margin-left: -3px;
    }
    #mealRedpacket-index .content .submit-container .final-price .old-price{
		color: #B4B3B0;
		font-size: 14px;
		text-decoration: line-through;
    }
    #mealRedpacket-index .content .submit-container .final-price .old-price span{
    	margin-left: -3px;
    }
    #mealRedpacket-index .content .submit-container .submit-button{
    	display: block;
		padding: 0 10px;
		min-width: 80px;
		height: 100%;
		text-align: center;
		font-size: 18px;
		color: #fff;
		background-color: #ff2d4b;
    }
     #mealRedpacket-index .content .buy-record{
    	display: block;
    	padding: 10px 15px;
    	font-size: 14px;
		color: #3F3F3F;
		line-height: 26px;
		overflow: hidden;
		background: #fff;
    }
    #mealRedpacket-index .content .buy-record .van-icon-right{
    	float: right;
    	font-size: 14px;
    	line-height: 26px;
    }
</style>


